.btn {
	cursor: pointer;
  }

  .btn:hover, .btn:focus {
	color: $gris;
	outline: 0;
  }

  .third {
	border-color: #3498db;
	color: $gris;
	box-shadow: 0 0 40px 40px #3498db inset, 0 0 0 0 #3498db;
	-webkit-transition: all 150ms ease-in-out;
	transition: all 150ms ease-in-out;
  }
  
  .third:hover {
	box-shadow: 0 0 10px 0 #3498db inset, 0 0 10px 4px #3498db;
  }
  
 .sweet {
	color: $naranja;
 }


 